button:active, button:focus {
  outline: none;
}

input {
  -webkit-box-shadow: none;
}

img:active, img:focus {
  outline: none;
}

@media (min-width: 768px) {
  .lines_wrapper {
    margin: 0 0 0 588px;
    width: 178px;
    height: 787px;
  }

  .lines_reverse_wrapper {
    width: 535px;
  }
}

@media (min-width: 1024px) {

  .lines_wrapper {
    margin: 0 0 0 590px;
    width: 434px;
    height: 787px;
  }

  .lines_reverse_wrapper {
    width: 791px;
  }
}

@media (min-width: 1280px) {
  .lines_wrapper {
    margin: 0 0 0  590px;
    width: auto;

    min-width: 604px;
    height: 785px;
  }

  .lines_reverse_wrapper {
    width: 958px;
  }
}

.lines_reverse_wrapper{
  background-image: url("./assets/image/lines.svg");
  z-index: -1;
  position: absolute;
  height: 1516px;
  margin-top: 782px;
  margin-left: 233px;
  background-size: cover;
}

.lines_wrapper {
  z-index: 0;
  background-image: url("../src/assets/image/lines-copy-3.svg");
  position: absolute;
  background-size: cover;
}




