%textHandsOfStyle {
  font-size: 23px;
  opacity: 0.9;
  font-family: 'Patrick Hand', cursive;
  color: #21272e;
}

.section {
  height: 1168px;
  background-image: linear-gradient(to top, #fafafc, rgba(255, 255, 255, 0.45));
}

.section_content {
  margin: 0 auto;
  min-width: 250px;
  max-width: 360px;
}

.contact_block {
  text-align: center;
  margin: -8px auto 52px;
}

.header_text {
  padding-left: 0;
  padding-top: 65px;
}

.header_text h3 {
  font-size: 13px;
  font-weight: bold;
  letter-spacing: 1px;
  color: #f43939;
  margin: 0;
}

.header_text h1 {
  margin: 11px auto;
  line-height: 39px;
  font-size: 28px;
  max-width: 330px;
  font-weight: bold;
  color: #21272e;
}

.arrow_wrapper img {
  margin-top: 25px;
  margin-left: -94px;
  width: 120px;
  height: 70px;
  object-fit: contain;
  transform: scale(-1, 1);
}

.arrow_wrapper p {
  margin: -45px 0px 28px 116px;
  position: static;
  text-align: left;
  height: 29px;
  font-size: 23px;
  @extend %textHandsOfStyle;
}

.arrow_lang_wrapper img {
  margin-left: 75px;
  width: 107px;
  height: 64px;
  object-fit: contain;
}

.arrow_lang_wrapper p {
  margin: -48px 0 22px 47px;
  position: static;
  width: 246px;
  text-align: left;
  height: 29px;
  font-size: 23px;
  @extend %textHandsOfStyle;
}

.language_block {
  margin: 0 auto;
}

.violet_circle {
  position: absolute;
  z-index: -1;
  background: #f1dcff;
  border-radius: 50%;
}

.pink_circle {
  position: absolute;
  z-index: -1;
  background: #FFC2AC;
  border-radius: 50%;
}

.blue_circle {
  position: absolute;
  z-index: -1;
  background: #87CEEB;
  border-radius: 50%;
}

@media (min-width: 768px) {
  .section {
    height: 1124px;
  }

  .section_content {
    margin: 60px auto;
    width: 768px;
    min-width: 768px;
  }

  .violet_circle {
    width: 426px;
    height: 426px;
    margin-left: -135px;
  }

  .blue_circle {
    width: 160px;
    height: 160px;
    margin-top: 458px;
    margin-left: -45px;
  }

  .pink_circle {
    width: 256px;
    height: 256px;
    margin-left: 330px;
    margin-top: 179px;
  }

  .contact_block {
    text-align: center;
  }

  .header_text {
    padding-left: 31px;
    padding-top: 49px;
  }

  .header_text h1 {
    margin: 20px auto;
    line-height: 40px;
    font-size: 32px;
    max-width: 506px;
  }

  .arrow_wrapper {
    margin-left: 200px;
  }

  .arrow_wrapper img {
    margin-top: 43px;
    margin-left: -400px;
  }

  .arrow_wrapper p {
    margin: -41px 0px 35px 69px;
  }

  .language_block {
    margin: -27px 0 0 0;
  }

  .arrow_lang_wrapper img {
    margin-left: 352px;
  }

  .arrow_lang_wrapper p {
    margin: -45px 0 22px 323px;
  }
}


@media (min-width: 1024px) {
  .section {
    height: 760px;
  }

  .section_content {
    margin: -26px auto;
    width: 1024px;
    min-width: 1024px;
  }

  .violet_circle {
    margin-left: -114px;
  }

  .pink_circle {
    margin-left: 350px;
    margin-top: 195px;
  }

  .contact_block {
    text-align: left;
    display: flex;
    margin: 162px 44px 110px 76px;
  }

  .header_text {
    margin-top: 84px;
    padding-top: 0;
  }

  .header_text h1 {
    margin: 12px 0 0 -63px;
    line-height: 58px;
    font-size: 40px;
    width: 400px;
  }

  .header_text h3 {
    margin: 6px 0px 0 -61px;
  }

  .form_block {
    margin-left: 130px;
  }

  .arrow_wrapper {
    margin-left: 29px;
  }

  .arrow_wrapper img {
    margin-top: 0;
    margin-left: 0;
  }

  .arrow_wrapper p {
    margin: -41px 0 139px 42px;
  }

  .language_block {
    margin: -440px 0 0 75px;
  }

  .arrow_lang_wrapper img {
    margin-left: -15px;
  }

  .arrow_lang_wrapper p {
    margin: -45px 0 22px -52px;
  }
}

@media (min-width: 1280px) {
  .pink_circle {
    margin-left: 345px;
    margin-top: 195px;
  }

  .section {
    height: 684px;
  }

  .section_content {
    margin: 0 auto;
    width: 1280px;
    min-width: 1280px;
  }

  .contact_block {
    margin: 116px 148px 125px 105px;
  }

  .header_text h1 {
    width: 490px;
  }

  .form_block {
    margin-left: 154px;
  }

  .arrow_wrapper p {
    margin: -41px 0 35px 42px;
  }

  .language_block {
    margin: -327px 0px 91px 98px;
  }

  .arrow_lang_wrapper img {
    margin-left: -52px;
  }

  .arrow_lang_wrapper p {
    margin: -45px 0 22px -76px;
  }
}





